body,
html {
  min-height: 2000px;
  margin: 0px;
  /*scroll-snap-type: y proximity;*/
  padding-bottom: 400px;
}

.main {
  min-height: 2000px;
  margin: 0px;
  padding-top: 40px;
}

.button {
  background-color: rgba(255, 255, 255, 0.79);
  height: 50px;
  width: 50px;
  margin-bottom: 40px;
  position: fixed;
  mix-blend-mode: exclusion;
  z-index: 5;
  cursor: pointer;

  font-weight: 700;
  color: rgb(45, 45, 45);
  overflow-wrap: break-word;
  text-align: center;
  align-items: center;
  vertical-align: middle;
  font-size: 30pt;
}

.body {
  height: fit-content !important;
  width: 100%;
  position: absolute;
  top: 0px;
  margin: auto;
  display: flex;
  flex-direction: column;
}

.wrap-head {
  padding-top: 50px;
  width: 80vw;
  height: 95vh;
  margin: auto;

  scroll-snap-align: start;
  scroll-snap-stop: always;
}

.description {
  color: #d2d2d2;
  padding-top: 200px;
  font-weight: 500;
  font-size: 3vw;
  line-height: 3.6vw;
  mix-blend-mode: color-dodge;
  transition: color 0.3s ease-in-out;
  container-name: main-text;
}

.description:hover {
  color: #bfbfbf;
}

.description a {
  text-decoration: none;
  mix-blend-mode: inherit;
  color: #d2d2d2;
  transition: color 0.3s ease-in-out;
}

.description:hover > a {
  text-decoration: none;
  mix-blend-mode: inherit;
  color: #eeeeeeea;
}

.wrap-galaxy {
  position: sticky;
  top: -30vh;
  z-index: -1;
  width: 90vw;
  margin: auto;
  padding-top: 100px;
}

.wrap-projs {
  height: fit-content;
  scroll-snap-align: start;
  scroll-snap-stop: always;
}

.footer {
  text-align: center;
  display: flex;
  align-items: center;
  height: 50px;
  margin: 0px auto;
  align-self: center;
  margin-top: 300px;
  margin-bottom: 60px;
  opacity: 0.6;
}

@media only screen and (max-width: 900px) {
  .description {
    font-size: 1.8rem;
    line-height: 2.2rem;
  }
}

@media only screen and (max-width: 800px) {
  .button {
    position: relative;
  }
  .description {
    color: #d8d8d8;
    padding-top: 20px;
    font-weight: 500;
    font-size: 1.5rem;
    line-height: 2rem;
    mix-blend-mode: color-dodge;
  }
  .wrap-head {
    height: auto;
    margin-bottom: 150px;
  }
}

/* Cute heart beating anim */

.centered-box {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.heart-con {
  display: block;
  width: 40px !important;
}

.heart {
  font-size: inherit;
}

.animate-beat {
  animation: beat 2s infinite;
}

@keyframes beat {
  0% {
    transform: scale(0.8);
    font-size: 13pt;
  }
  40% {
    transform: scale(1);
    font-size: 18pt;
  }
  60% {
    transform: scale(0.8);
    font-size: 12pt;
  }
  80% {
    transform: scale(1);
    font-size: 20pt;
  }
  100% {
    transform: scale(0.8);
    font-size: 13pt;
  }
}
