.projects-wrapper .card {
  width: auto;
  height: 100%;
  background-color: rgb(39, 39, 39);
  border-radius: 16px;
  box-shadow: 0px 10px 15px 3px rgba(0, 0, 0, 0.112);
  cursor: pointer;
  overflow: hidden;
}

.card .icon-wrap {
  position: relative;
  top: 10px;
  left: 10px;
  background-color: var(--black);
  width: 50px;
  height: 50px;
  margin-top: -50px;
  transform: translateY(50px);
  border-radius: 15px;
  display: flex;
  justify-content: center;
}
.card .icon-wrap img {
  align-self: center;
  width: 32px;
  height: 32px;
}

/* card types */

/********************************************************* PROJECT CARD ***********************************/

.card-project .img-wrap {
  height: 62%;
  width: 100%;
  overflow: hidden;
  transition: all ease-in-out 0.25s;
}

.card-project img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.card-project .info-wrap {
  padding: 15px 20px;
}

.card-project h3 {
  font-size: 15pt;
  font-weight: 600;
  margin: 10px 0px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.card-project p {
  color: rgb(126, 126, 126);
  font-size: 11pt;
  line-height: 23px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2; /* number of lines to show */
  line-clamp: 2;
  -webkit-box-orient: vertical;
}

.card-project:hover > div.img-wrap {
  height: 57%;
}

/********************************************************* EXHIBIT CARD ********************************************/

.card-exhibit img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: all ease-in-out 0.2s;
}
.card-exhibit:hover > img {
  transform: scale(1.1);
}

.card-exhibit .info-wrap {
  position: relative;
  margin-top: -110px;
  height: 120px;
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
  background: -webkit-gradient(
    linear,
    0 0%,
    0 100%,
    from(rgba(0, 0, 0, 0)),
    to(rgb(0, 0, 0))
  );
}
.card-exhibit .info-wrap h2 {
  mix-blend-mode: normal;
  margin: 30px 20px;
  font-size: 16pt;
}

/******************************************************************** POPOVER STYLING ***********************************/

.darken-bg {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.751);
  z-index: 999;
}

.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;

  z-index: 1000;
  display: flex;
  justify-content: space-around;
  overflow-y: scroll;
}

.scrollable {
  margin-top: 25vh !important; /* this leaves 10vh of margin on top */
  border-radius: 16px;
  border: 1px rgba(128, 128, 128, 0.273) solid;
  box-shadow: 0px 0px 20px 5px rgba(0, 0, 0, 0.322);
  width: 70vw;
  max-width: 800px;
  height: fit-content;
  margin: auto;
  margin-bottom: 100px;
  overflow: hidden;
  background-color: rgb(24, 24, 24);
}

.scrollable .cover {
  width: 100%;
  height: 30vh;
  object-fit: cover;
}

.scrollable .title {
  font-size: 20pt !important;
  margin-top: 20px !important;
  margin-bottom: 30px !important;
}

.popup-content {
  padding: 10px 35px;
  height: auto;
  margin-bottom: 20px;

  font-size: 11.5pt;
  line-height: 24px;
  color: rgb(209, 209, 209);
  cursor: default;
}
.popup-content p {
  margin: 0px 0px;
  margin-bottom: 15px;
}
.popup-content a {
  color: rgb(234, 234, 234);
  text-decoration: none;
  font-weight: bold;
  border-bottom: 2px rgb(179, 179, 179) solid;
}
.popup-content ul {
  list-style-position: outside;
  margin-left: 0;
  padding-left: 20px;
  margin-top: 0px;
}
.popup-content li {
  padding-left: 10px;
  margin-bottom: 10px;
}

.popup-content h2 {
  font-size: 15pt !important;
  margin: 20px 0px !important;
  margin-top: 40px !important;
  margin-bottom: 10px !important;
}

.popup-content img {
  width: 100%;
}

.popup-content video {
  margin: 20px auto;
  max-width: 90%;
  width: auto;
  height: 300px;
  object-fit: cover;
  border-radius: 10px;
  box-shadow: 0px 5px 10px 5px rgba(0, 0, 0, 0.188);
}

.popup-content caption {
  font-size: 10pt;
  color: rgb(164, 164, 164);
  margin-bottom: 30px;
}

.image {
  width: 100%;
  height: auto;
}

.popup-content-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.linkbtn {
  background-color: rgb(52, 52, 52);
  color: white;
  height: fit-content;
  padding: 5px 10px;
  border-radius: 10px;
  text-decoration: none;
  padding-left: -2px;
  padding-bottom: -2px;
}

/************* RESPONSIVITY **********************/

@media only screen and (max-width: 1000px) {
  .card-exhibit .info-wrap h3,
  .card-project h3 {
    font-size: 13pt;
    margin-top: 5px;
  }
  .card p {
    font-size: 10pt;
  }
  .scrollable {
    width: 90%;
    max-width: 90%;
  }
}
