.projects-wrapper {
  width: 100vw;
}

.projects-wrapper section {
  width: 70vw;
  max-width: 800px;
  margin: auto;
  margin-bottom: 100px;
}

.projects-wrapper section h2 {
  font-weight: 600;
  margin-bottom: 30px;
  font-size: 20pt;
  mix-blend-mode: color-dodge;
  color: rgb(228, 228, 228);
}

.projects-wrapper .grid {
  display: grid;
  gap: 25px;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: 180px !important;
  grid-auto-rows: 180px;
}

.projects-wrapper .col-span-2 {
  grid-column: span 2;
}
.projects-wrapper .row-span-2 {
  grid-row: span 2;
}

@media only screen and (max-width: 1000px) {
  .projects-wrapper .grid {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 150px !important;
    grid-auto-rows: 150px;
    gap: 30px;
  }
  .projects-wrapper .col-span-2 {
    grid-column: span 1;
  }
  .projects-wrapper .row-span-2 {
    grid-row: span 2;
  }
}

@media only screen and (max-width: 600px) {
  .projects-wrapper .grid {
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: 140px !important;
    grid-auto-rows: 140px;
    gap: 30px;
  }
  .projects-wrapper .col-span-2 {
    grid-column: span 1;
  }
  .projects-wrapper .row-span-2 {
    grid-row: span 2;
  }
  .grid .card {
    grid-row: span 2;
  }
}
